    width: 100%;
}
.btn {
    background-color: #6b7a8f;
    background-color: var(--color-primary);
    border: none;
    border-radius: 0.125rem;
    color: white;
    cursor: pointer;
    font-family: inherit;
    font-size: inherit;
    padding: 1rem 2.5rem 1.125rem;
}
.btn:focus {
    outline-color: #6d64f7;
    outline-color: var(--color-focus);
    outline-offset: 2px;
    outline-style: solid;
    outline-width: 3px;
}
.btn:active {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
}
.slider__controls {
    display: flex;
    justify-content: center;
    position: absolute;
    top: calc(100% + 1rem);
    width: 100%;
}
.slider__controls .btn {
    --size: 3rem;
    align-items: center;
    background-color: transparent;
    border: 3px solid transparent;
    border-radius: 100%;
    display: flex;
    height: var(--size);
    padding: 0;
    width: var(--size);
}
.slider__controls .btn:focus {
    border-color: #6d64f7;
    border-color: var(--color-focus);
    outline: none;
}
.slider__controls .btn--previous > * {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.slider {
    --slide-size: 70vh;
    --slide-margin: 4vh;
    height: var(--slide-size);
    margin: 0 auto;
    position: relative;
    width: var(--slide-size);
}
.slider__wrapper {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    margin: 0 calc(var(--slide-margin) * -1);
    position: absolute;
    -webkit-transition: -webkit-transform 600ms cubic-bezier(0.25, 1, 0.35, 1);
    transition: -webkit-transform 600ms cubic-bezier(0.25, 1, 0.35, 1);
    transition: transform 600ms cubic-bezier(0.25, 1, 0.35, 1);
    transition: transform 600ms cubic-bezier(0.25, 1, 0.35, 1), -webkit-transform 600ms cubic-bezier(0.25, 1, 0.35, 1);
    -webkit-transition: -webkit-transform var(--base-duration) cubic-bezier(0.25, 1, 0.35, 1);
    transition: -webkit-transform var(--base-duration) cubic-bezier(0.25, 1, 0.35, 1);
    transition: transform var(--base-duration) cubic-bezier(0.25, 1, 0.35, 1);
    transition: transform var(--base-duration) cubic-bezier(0.25, 1, 0.35, 1), -webkit-transform var(--base-duration) cubic-bezier(0.25, 1, 0.35, 1);
}
